html,
.root {
    font-size: 15px;
    word-spacing: 1px;
    -ms-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
}

body,
.article {
    font-family: 'Roboto', sans-serif;
    font-size: 1em;
    color: $dark-grey;
    font-weight: 400;
    letter-spacing: normal;
}

h1,
.h1 {
    font-size: 28px;
    line-height: 1.4em;
    margin-top: 1em;
    margin-bottom: 1em;
    font-weight: 800;
}

h2,
.h2 {
    font-size: 25px;
    line-height: 1.5em;
    margin-top: 0.5em;
    margin-bottom: 1.04em;
    font-weight: 800;
}

h3,
.h3 {
    font-size: 21px;
    line-height: 1.6em;
    margin-bottom: 1em;
    font-weight: 800;
}

h4,
.h4 {
    font-size: 1em;
    line-height: 1.6;
    margin-top: 1em;
    margin-bottom: 1em;
    font-weight: 800;
}

h5,
.h5 {
    font-size: 1em;
    line-height: 1.73333333em;
    margin-top: 1.73333333em;
    margin-bottom: 0em;
    font-weight: 800;
}

h1.page-name {
    font-size: 25px;
}

p {
    font-size: 1em;
    line-height: 1.6em;
}

a.active.focus,
a.active:focus,
a.focus,
a:active.focus,
a:active:focus,
a:focus,
button.active.focus,
button.active:focus,
button.focus,
button:active.focus,
button:active:focus,
button:focus,
.btn.active.focus,
.btn.active:focus,
.btn.focus,
.btn:active.focus,
.btn:active:focus,
.btn:focus {
    outline: 0 !important;
    outline-color: transparent !important;
    outline-width: 0 !important;
    outline-style: none !important;
    box-shadow: 0 0 0 0 rgba(0, 123, 255, 0) !important;
}

.text-grey {
    color: $midi-grey;
}

.text-dgrey {
    color: $half-dark-grey;
}

.text-xs {
    font-size: 12px;

    i {
        font-size: 15px;
    }
}

.p-descr {
    display: none;
    font-size: 12px;
    color: $midi-grey;
}

.p-page-cap {
    font-weight: 600;
    font-size: 22px;
    margin-top: 0.5em;
    margin-bottom: 1em;
}

@media (min-width: 768px) {
    .p-descr {
        display: inline-block;
    }
}

.p-main-price {
    font-size: 25px;
    font-weight: 800;
    color: #ff3495;
}

a {
    color: inherit;
    text-decoration: none;
}

a:hover {
    text-decoration: none;
    color: inherit;
}

.a-link {
    color: $link;
    text-decoration: underline;
}

.a-link:hover,
.a-link:focus {
    text-decoration: none;
    color: $link;
}

.a-border-dotted {
    text-decoration: none;
    position: relative;
    border-bottom: 1px dashed;
}

.a-border-dotted:hover,
.a-border-dotted:focus {
    border-color: #fff;
    outline: none;

}

.bordered {
    border-bottom: 3px solid #fee44c;
    display: inline-block;
}

.a-zoom-img {
    display: block;
    border: 1px solid #fff;
}

.a-zoom-img:hover {
    // border: 1px solid $base-color;
}

.header {
    color: inherit;
    line-height: 1.2em;
    padding-top: 1.1em;
    padding-bottom: 1em;
    box-shadow: 0px 0px 15px #2e303847;


    a {
        font-size: 13px;
    }

    a:not(.navbar-brand):hover {
        text-decoration: underline;
    }

    .a-city {
        position: relative;
        display: block;
        font-weight: 700;
    }

    .a-city::after {
        display: inline-block;
        margin-left: .255em;
        vertical-align: .15em;
        content: "";
        border-top: .3em solid;
        border-right: .3em solid transparent;
        border-bottom: 0;
        border-left: .3em solid transparent;
    }

    .phone-box {
        max-width: 180px;
        line-height: 1.3em;

        p {
            font-size: 0.75em;
            color: $midi-grey;
        }
    }

    // иконки svg
    .header-svg {
        fill: #2e3038;
        fill-rule: evenodd;
    }


    .mob-search-close {
        fill: #a92727;
        fill-rule: evenodd;
    }

    // -- иконки svg

    .header-price {
        font-weight: 600;
        // font-size: 1.2em;
    }

    .img-basket {
        width: 28px;
        height: auto;
    }

    @media(min-width: 992px) {
        box-shadow: none;
        margin-bottom: 0;
    }

}

.price-rouble:after {
    content: "\A0\20BD";
}

.nowrp {
    white-space: nowrap;
}

