@font-face {
    font-family: "onlinewebfonts";
    src: url("../fonts/webfont/onlinewebfonts.eot");
    src: url("../fonts/webfont/onlinewebfonts.eot?#iefix") format("embedded-opentype"),
        url("../fonts/webfont/onlinewebfonts.woff2") format("woff2"),
        url("../fonts/webfont/onlinewebfonts.woff") format("woff"),
        url("../fonts/webfont/onlinewebfonts.ttf") format("truetype"),
        url("../fonts/webfont/onlinewebfonts.svg#onlinewebfonts") format("svg");
    font-weight: normal;
    font-style: normal;
}

[class^="i_"]:before,
[class*=" i_"]:before {
    font-family: "onlinewebfonts";
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    // font-size: 1.4em;
    margin-right: .5em;
    -webkit-font-smoothing: antialiased;
}

.i_tape:before {
    content: "\e010";
}

.i_delivery:before {
    content: "\e011";
}

.i_adhesive_tape:before {
    content: "\e012";
}

.i_measuring_tape:before {
    content: "\e013";
}

.i_measure_scale:before {
    content: "\e014";
}

.i_measure_scale_6:before {
    content: "\e015";
}

.i_measure_scale_7:before {
    content: "\e016";
}

.i_guarantee:before {
    content: "\e017";
}

.i_ask:before {
    content: "\e018";
}



// Был иконочный шрифт
.search-form {
    position: relative;
    flex-grow: 1;

    input {
        height: 30px;
        background-color: #fff;
        border-radius: 0;
        border: none;
        border-bottom: 2px solid $base-color;
        font-size: 0.8em;
        padding-left: 0;

    }

    .search-btn {
        background-color: #fff;
        border-radius: 0;
        border: none;
        border-bottom: 2px solid $base-color;
        height: 30px;
        padding-left: 0;
        padding-right: 0;
    }

    .img-search {
        width: 17px;
        height: auto;
        margin-top: -8px;
        shape-rendering: crispEdges;
    }

    @media (min-width: 992px) {
        input {
            height: 40px;
            background-color: $mini-grey;
            border: 1px solid #dedede;
            font-size: 0.8em;
            border-top-right-radius: 0;
            border-bottom-right-radius: 0;
            border-bottom-left-radius: 3px;
            border-top-left-radius: 3px;
            border-right: none;
            padding-left: 0.75rem;

        }

        .search-btn {
            height: 40px;
            border: 1px solid #dedede;
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;
            border-top-right-radius: 3px;
            border-bottom-right-radius: 3px;
            background-color: $mini-grey;
            border-left: none;
            width: 50px;
        }

        .img-search {
            margin-top: -7px;

        }

    }
}

.basket-box {
    position: relative;
    // transform: translateZ(0);

    .count {
        background-color: $base-color;
        height: 20px;
        width: 20px;
        border-radius: 100%;
        margin-top: -5px;
        margin-left: -5px;
        font-size: 11px;
        text-align: center;
        display: inline-block;
        line-height: 20px;
        font-weight: 700;
    }
}

.navbar {
    padding: 0;
    border-radius: 3px;

    .img-logo {
        height: 24px;
        width: auto;
        display: inline-block;
        vertical-align: text-bottom;
        margin-right: 4px;
        margin-top: 0px;
        margin-bottom: 1px;
    }

    .navbar-brand p {
        text-transform: uppercase;
        font-weight: 800;
        display: inline-block;
        margin-bottom: 0;
        line-height: 14px;
        font-size: 11px;
        letter-spacing: 0.08em;
        vertical-align: bottom;

        span {
            color: $btn-color;
        }
    }

    @media (min-width: 992px) {
        .navbar-brand p {
            line-height: 19px;
            font-size: 14px;
            letter-spacing: .06em;
            vertical-align: top;
        }

        .img-logo {
            // width: 153px;
            height: 30px;
            margin-top: 4px;
        }
    }

    .navbar-toggler {
        outline: none;
        padding-left: 0;
        z-index: 500;
        width: 28px;
    }



}


.yel-bck {
    background-color: $base-color;
}

.navbar-my-nav {
    width: 100%;
    background-color: $base-color;
    border-radius: 3px;
    padding: 0 10px;
    position: absolute;

    .dropdown-menu {
        .dropdown-item {
            font-weight: 500;
        }
    }

    a {
        color: inherit;
        // text-decoration: none;
    }

    .a-city {
        border-bottom: 1px dashed;
        padding-top: 20px;
        margin-bottom: 40px;
    }

    @media (min-width: 992px) {
        height: 50px;
        position: relative;
    }

    .phone-p {
        font-size: 1.3em;
    }

    .nav-link {
        color: inherit;
        font-weight: 800;
        text-transform: uppercase;
        font-size: 14px;
        padding-left: 25px;
        border-bottom: 1px solid #fdea79;
    }

    .mobile-nav-link {
        text-decoration: underline;
        font-size: 0.9em;
        text-transform: uppercase;
        font-weight: 600;
        padding: 15px;
        display: block;
        border-top: 1px solid #fbeb8f;
    }

    .mobile-dop-info {
        margin-bottom: -15px;
        padding: 25px;
        background-color: #3f4a50;
        color: #f1f5f6;

        .a-city {
            text-decoration: none;
        }
    }

    .mobile-dop-link {}

    .cls-1 {
        fill: $midi-grey;
    }

    .line {
        width: 100%;
        height: 1px;
        background-color: #fde963;
    }

    .caption {
        font-weight: 800;
    }

    .description {
        font-weight: 400;
        font-size: 0.7em;
        display: block;
        color: transparentize($mini-grey, .6);

    }

    .dropdown-menu {
        font-size: inherit;
        width: 100%;
        border: none;
        background-color: #0000000a;
        border: 1px solid #fee44c;

        @media (min-width: 992px) {
            background-color: white;
            border: 1px solid #dedede;


        }

        .dropdown-item:focus,
        .dropdown-item:hover {
            background-color: #0000000a;
        }
    }
}

@media (min-width: 992px) {
    .navbar-my-nav {
        .line {
            width: 1px;
            height: 35px;
        }

        .nav-link {
            border-bottom: none;
        }
    }
}

// Стили мобильного меню 
.bsnav-mobile,
.bsnav-mobile .bsnav-mobile-overlay,
.bsnav-sticky.navbar-fade,
.bsnav-sticky.navbar-slide {
    left: 50px;
    position: fixed;
    top: 0;
    right: 0;
}

.navbar-toggler .navbar-toggler-icon,
.navbar-toggler .navbar-toggler-icon::after,
.navbar-toggler .navbar-toggler-icon::before {
    background-color: #212529;
}

.bsnav-mobile {
    bottom: 0;
    z-index: 49;
    pointer-events: none;
}

.bsnav-mobile .navbar {
    background: $base-color;
    padding: 15px 0 0;
    flex-flow: column;
    top: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    transition: .4s ease-in-out;
    transform: translate3d(400px, 0, 0);
    overflow: auto;
}

.bsnav-mobile.in .navbar {
    transform: translate3d(0, 0, 0);
    width: 100%;
}

.bsnav-mobile .navbar-nav {
    padding: 0;
}

.bsnav-mobile .navbar-nav {
    @media (min-width: 992px) {
        padding: 0;
    }
}

.ul-dop-info {
    list-style: inherit;
    padding: 0;
    margin: 0;
}

// Остальные на главной
.phone,
.header .phone {
    font-size: 1.1em;
    font-weight: 700;
}

.banner-box {
    margin-bottom: 30px;
}

.main-banner-box {
    background-color: $mini-grey;
    position: relative;
    overflow: hidden;
    z-index: -1;

    a {
        color: inherit;
    }

    .main-banner-img {}

    .text-banner {
        position: absolute;
        z-index: 3;
        padding: 20px;
    }

    .p-banner-cap {
        font-size: 2.2em;
        line-height: 1.57575758em;
        margin-top: 0.78787879em;
        margin-bottom: 0.6em;
        font-weight: 800;
    }

    @media (min-width: 992px) {
        .p-banner-cap {
            font-size: 3em;
        }

        .text-banner {
            background-color: #fee44c00;
        }

        .main-banner-box {}
    }
}

.pluses-box {
    margin-top: 2em;
    overflow: hidden;

    p {
        font-size: 0.7em;
        font-weight: 700;
        margin-bottom: 0;
        text-transform: uppercase;
        line-height: 1.5em;
    }

    img {
        width: 41px;
        height: auto;
    }

    @media(min-width: 992px) {
        p {
            font-size: 0.8em;
        }
    }

}

.pop-a {
    margin-right: 20px;
    color: inherit;
    font-weight: 500;
    text-decoration: underline;
    margin-bottom: 20px;
    white-space: nowrap;
    font-size: 0.8em;

}

@media (min-width: 992px) {
    .pop-a {
        border: 1px solid #edeef0;
        padding: 10px 20px;
        font-size: inherit;
        text-decoration: none;
        display: inline-block;
        color: inherit;

    }

    .pop-a:hover {
        background-color: $mini-grey;
        text-decoration: none;
        color: inherit;
    }
}



// Модальные окна
.my-modal {
    .form-group {
        position: relative;
        margin-bottom: 30px;
    }

    .form-control {
        background-color: $mini-grey;

    }

    label {
        font-size: 0.8em;
        line-height: 1.4em;
        color: $half-dark-grey
    }

    .my-label {
        position: absolute;
        top: -1em;
        background-color: #fff;
        left: 1em;
    }

    .my-file {
        padding: 20px;
        border: 1px dashed $midi-grey;
        width: 100%;
        background-color: $mini-grey;

        input {
            width: inherit;
            font-size: 0.8em;
        }
    }

    .modal-content {
        padding: 30px;
    }

    .btn-close {
        position: absolute;
        top: 10px;
        right: 10px;
        width: 40px;
        height: 40px;
    }

    .city-ul {
        list-style: none;
        margin: 0;
        padding: 0;
    }

    a {
        color: inherit;
    }

    @media(min-width: 768px) {
        .modal-content {
            padding: 40px 60px 50px;
        }
    }
}

// Хлебные крошки
.breadcrumb-box {
    margin-top: 0.5em;
    width: 100%;
}

.a-back {
    display: inline-block;
    padding: 2px 10px 6px 0px;
    border: none;

    svg {
        fill: $dark-grey;
        height: 16px;
        width: 16px;
        transform: rotate(180deg);
    }

    span {
        display: none;
    }

}

.a-back:hover {
    background-color: #fff;
    outline: none;

    svg {
        fill: $btn-color;
    }
}

.a-share {
    background-color: #fff0;
    padding-left: 8px;
    height: 30px;
    width: auto;
    display: inline-block;
    border: none;
    position: relative;

    svg {
        fill: $dark-grey;
    }
}

.a-share:hover {
    outline: none;
    background-color: #fff0;

    svg {
        fill: $btn-color;
    }

}

@media (min-width: 768px) {

    .a-back {
        display: inline-block;
        padding: 0px 12px 3px 8px;
        margin-right: 10px;
        border: 1px solid #f2f5f6;
        border-radius: 3px;

        svg {
            fill: #2e3038;

        }

        span {
            display: inline;
            font-size: 0.75em;
            font-weight: 700;
            margin-left: 3px;
        }

        .a-share {
            background-color: #fff;
        }

    }

    .a-back:hover {
        border-color: $btn-color;
    }

    .a-share {
        width: 36px;
        height: 36px;
        padding: 0;
        border: 1px solid $mini-grey;
        border-radius: 50%;

        svg {
            margin-bottom: 2px;
            margin-left: -2px;
        }
    }
}

.nav-my-breadcrumbs {
    width: calc(100% - 62px);
    display: inline-block;
    height: 30px;
    vertical-align: middle;

    @media (min-width: 768px) {
        width: calc(100% - 133px);
    }
}

.my-breadcrumb {
    display: flex;
    flex-wrap: nowrap;
    margin-bottom: 0;
    list-style: none;
    padding-left: 0;
    margin-top: 0.25em;

    .slick-slide {
        padding-right: .5em;
        display: inline-flex;
    }

    .slick-slide+.slick-slide {
        // padding-left: .5rem;
    }

    .my-breadcrumb-item {
        font-size: 12px;
        color: $midi-grey;
    }

    .my-breadcrumb-item.active {
        color: $gray-400;
        cursor: default;
    }

    .brc {
        margin: 0 4px 0 8px;
        display: inline-block;
        vertical-align: top;
        height: 16px;
        width: 16px;
        color: $gray-300;
    }

    // .slick-slide+.slick-slide::before {
    //     content: "";
    //     color: $midi-grey;
    //     display: inline-block;
    //     padding-right: .5rem;
    //     width: 
    // }
}

// Ссылки-выборки или подразделы на странице каталога
.submenu-nav {
    margin-bottom: .5em;

    .nav-item {
        margin-right: 1em;

        .nav-link {
            color: $link;
            padding-left: 0;
            text-decoration: underline;
            padding-right: 5px;
            padding-top: 0;
        }

        .nav-link:hover,
        .nav-link:focus {
            text-decoration: none;
            color: transparentize($link, 0.3);
        }
    }

    @media (min-width: 992px) {
        .nav-item {
            margin-right: 1.5em;

        }
    }
}

// Характеристики с отбивкой

.p-values {
    position: relative;
    width: 100%;
    line-height: 1em;
    font-weight: 500;
    margin-bottom: 1em;
    font-size: 14px;


    .feature-name {
        word-wrap: break-word;
        overflow-wrap: break-word;
        position: relative;
        vertical-align: top;
        display: inline-block;
        width: 55%;
        margin: 0 2.5% 0 0;
        color: $half-dark-grey;

        b {
            font-weight: 400;
            background: #fff;
            padding: 0 10px 0 0;
            position: relative;
            z-index: 1;
            color: inherit;
        }
    }

    .feature-name::after {
        border-bottom: 1px dashed #ccc;
        bottom: 0px;
        content: '';
        display: block;
        height: 20px;
        left: 0;
        position: absolute;
        width: 100%;
    }

    .feature-val {
        width: 40%;
        display: inline-block;
    }
}

@media (min-width: 1200px) {
    .values-box {
        .p-values {
            font-weight: 400;
        }
    }
}

.tmb-wrap-table div.active {
    border-color: $mini-grey;
}

// Фильтры и сортировки в каталоге  
select {
    appearance: none;
    background: url(../img/select-arr.svg) right 15px top 13px no-repeat #fff;
    background-size: 10px auto;
    outline: none;

}

.select-sort {
    border-color: $gray-200;
}

.filter-btn {
    margin: 0;
    padding: 5px 15px;
    background-color: #ffffff;
    border-radius: 3px;
    border: 1px solid $gray-200;
    height: 38px;
    position: relative;
}

.filter-btn-marked::after {
    content: "";
    position: absolute;
    background-color: #fee44c;
    height: 12px;
    width: 12px;
    border-radius: 50%;
    right: 2px;
    top: 2px;

}

a.filter-dop-btn {
    border-radius: 3px;
    background-color: white;
    border: 1px solid $gray-300;
    font-size: 13px;
    padding: 6px 20px;
    font-weight: 400;

}

a.clear-btn {
    color: $gray-300;

    &.active {
        background-color: $btn-color;
        color: #fff;
        border-color: $btn-color;
    }
}

select:focus {
    outline: none;
}

.filter-sort-box {
    margin-top: .5em;
    border-top: 1px solid #f2f5f6;
    padding-top: 1em;

    .form-control {
        font-size: 13px;
        font-weight: 500;
        padding-right: 40px;
    }

    .selects-wrap {
        padding: 20px 0px 20px 0;
