    width: 10px;
    height: 10px;
    margin: 0 5px;
    padding: 0;
    cursor: pointer;
}

.my-dots li button {
    font-size: 0;
    line-height: 0;
    display: block;
    width: 0px;
    height: 0px;
    padding: 3px;
    cursor: pointer;
    color: transparent;
    outline: none;
    background: transparent;
    border-radius: 50%;
    border: 1px solid #999ba2;
    position: relative;
}

.my-dots li button:hover,
.my-dots li button:focus {
    outline: none;
}

.my-dots li button:hover:before,
.my-dots li button:focus:before {
    // opacity: 1;
}

.my-dots li button:before {
    font-family: 'slick';
    width: 8px;
    height: 8px;
    border-radius: 4px;
    position: absolute;
    background-color: #fff;
    top: -1px;
    left: -1px;
    content: '';
    // font-size: 40px;
    // line-height: 0px;
    // position: absolute;
    // left: -3px;
    // content: '•';
    // text-align: center;
    opacity: .25;
    // color: #fff;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

}

.my-dots li.slick-active button:before {
    opacity: 1;
    background-color: $btn-color;
}

