// Это выдвигающиеся сбоку кнопки: заказать замер, калькулятор и задать вопрос;
.link-b {
    margin-top: 109px;
}

.link-c {
    margin-top: 218px;
}

.slick-list {
    overflow: hidden;
}

.schemes-slider {
    position: relative;
    padding: 0px 30px;

    .slick-track {
        // align-items: stretch;
        // display: flex;
    }

    .schemes-slider-item {
        width: calc(100% - 60px);
        text-align: center;

        a {
            background-color: #fff;
            width: 100%;
            display: block;
            height: 200px;
        }


        p {
            margin-top: 0;
            margin-bottom: 0;

        }
    }

    .slick-arrow {
        position: absolute;
        top: 40%;
        z-index: 10;
        font-size: 0;
        margin-top: -15px;
        border: none;
        outline: none;
        width: 20px;
        height: 30px;
        padding: 10px;

        &.slick-prev {
            background: #fff0 url(../img/prev-slide_pass.svg) center no-repeat;
            background-size: 15px 30px;
            left: 0;
        }

        &.slick-next {
            right: 0;
            background: #fff0 url(../img/next-slide_pass.svg) center no-repeat;
            background-size: 15px 30px;
        }

        &.slick-disabled {
            opacity: 0.2;
        }

    }

    img {
        max-height: 200px;
        width: auto;
        margin: auto;
        padding: 20px;
        // max-width: 220px;
    }

}

@media (min-width: 576px) {
    .schemes-slider {
        .schemes-slider-item {
            a {
                background-color: #fff;
                width: 100%;
                display: block;
                height: 200px;
            }


        }
    }
}

// Блок похожие

.similar {
    position: relative;

    .similar-item {
        padding: 15px;
    }


    .slick-arrow {
        position: absolute;
        top: 50%;
        width: 30px;
        border: none;
        height: 30px;
        outline: 0;
        margin-top: -35px;
        z-index: 1;
        font-size: 0;
        padding: 10px;
        border-radius: 50%;
        box-shadow: 0 0 10px rgba(105, 109, 125, 0.22);

        &.slick-next {
            background: #fff url(../img/forward.svg) center no-repeat;
            background-size: 8px 30px;
            right: 0px;
        }

        &.slick-prev {
            background: #fff url(../img/back.svg) center no-repeat;
            background-size: 8px 30px;
            left: 0px;
        }
    }
}

@media (min-width: 1450px) {
    .similar {

        .slick-arrow {

            &.slick-next {
                right: -10px;
            }

            &.slick-prev {
                left: -10px;
            }
        }
    }
}

